*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.dashboard-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 16px;
}

.chart-container {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  width: 100%;
}

.labels-container {
  width: 150px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.label-item {
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  white-space: nowrap;
}

.bar-chart-wrapper {
  flex: 1;
  min-width: 0;
}
